(function(e){try{e=angular.module("super-templates")}catch(t){e=angular.module("super-templates",[])}e.run(["$templateCache",function(c){c.put('components/super-viz-pie-chart/viz-pie-chart.html',`<div class="super-viz-pie-chart"><highchart config="vm.highchartInput"></highchart><a ng-href="{{vm.nextRoute}}">{{vm.nextRouteTitle}}</a></div>`);c.put('components/super-viz-sparkline/viz-sparkline.html',`<div class="super-viz-sparkline"><super-viz-highcharts-wrap config="vm.highchartConfig"></super-viz-highcharts-wrap></div>`);}])}())