import {Component} from 'base-component';
// #-----------------------------------------# //
// #---- Component (super-viz-sparkline) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	inputData: '=',
	widthOverride: '=',
	heightOverride: '='
};

// directive definition object overrides
const config = {
	// transclude: true
};

/*@ngInject*/
function vizSparkline() {

	class VizSparkline extends Component {
		/*@ngInject*/
		controller($scope, $log) {
			// view model for tracking state
			const vm = $scope.vm = { // eslint-disable-line no-unused-vars
			};

			// behavior that needs to be bound to the view
			const go = $scope.go = { // eslint-disable-line no-unused-vars
				click(evt) {
					vm.test = $scope.text;
				}
			};

			if (!$scope.inputData || !Array.isArray($scope.inputData)) {
				$log.error('sparkline expects an array');
			} else if ($scope.inputData.length < 0) {
				$log.warn('sparkline expects an array of non-zero values');
			} else {
				vm.highchartConfig = makeHighchartConfig($scope.inputData);
			}
		}
	}

	function makeHighchartConfig(arrayOfNumbers, widthOverride, heightOverride) {
		return {

			chart: {
				renderTo: 'container',
				margin: [0, 0, 0, 0],
				backgroundColor: 'transparent',
				width: widthOverride || 80,
				height: heightOverride || 50
			},
			title: {
				text: ''
			},
			credits: {
				enabled: false
			},
			xAxis: {
				labels: {
					enabled: false
				},
				categories: [''] // Note: I wouldn't recommend this for any highchart widget besides the sparkline. But in this case, I am trying to silence the superVizHighchartsWrap warnings about not having categories. Because this is probably the one case were it's actually okay.
			},
			yAxis: {
				maxPadding: 0,
				minPadding: 0,
				gridLineWidth: 0,
				endOnTick: false,
				labels: {
					enabled: false
				}
			},
			legend: {
				enabled: false
			},
			tooltip: {
				enabled: false
			},
			plotOptions: {
				series: {
					animation: false,
					enableMouseTracking: false,
					lineWidth: 1,
					shadow: false,
					states: {
						hover: {
							lineWidth: 1
						}
					},
					marker: {
						//enabled:false,
						radius: 0,
						states: {
							hover: {
								radius: 2
							}
						}
					}
				}
			},
			series: [{
				data: arrayOfNumbers
			}]

		};
	}

	return new VizSparkline(props, 'viz-sparkline', config);
}

// #-- END Component (super-viz-sparkline) --# //
// #-----------------------------------------# //

angular.module('super-suit-components').directive('superVizSparkline', vizSparkline);

