import {Component} from 'base-component';
// #-----------------------------------------# //
// #---- Component (super-viz-pie-chart) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	widgetTitle: '@', // The business-case title of the dashboard widget
	api: '@', // The endpoint that is serving the data for this dashboard widget
	nextRoute: '@', // The route that the user will be sent to if they click the 'See More...' link at the bottom.
	nextRouteTitle: '@', // The title of the link at the bottom
	attrForSectionTitle: '=', // The attribute from the api result that we will map to the pie chart sections
	attrForSectionValue: '=', // The attribute from the api result that we will map to the pie chart values
	mutationFunction: '@' // The mutation function you need to massage the api data into the right result
};

// directive definition object overrides
const config = {
	// transclude: true
};

const defaultConfig = {
	chart: {
		plotBackgroundColor: null,
		plotBorderWidth: null,
		plotShadow: false,
		type: 'pie'
	},
	// tooltip: {
	//     pointFormat: '{series.name}: <b>{point.percentage:.1f}%</b>'
	// },
	title: {
		text: '' // Default it, so that it doesn't show up as 'Chart Title'
	},
	plotOptions: {
		pie: {
			allowPointSelect: true,
			cursor: 'pointer',
			dataLabels: {
				enabled: true,
				format: '<b>{point.name}</b>: {point.percentage:.1f} %',
				style: {
					color: 'black'
				}
			},
			showInLegend: true
		}
	},
	series: [{
		name: 'Brands',
		colorByPoint: true,
		// innerSize: '70%', // Note: innerSize cuts the 'hole' into the 'donut' chart
		// data: [] // <-- this get's populated via the component props
	}]
};

const mockEndpointData = [
	{
		// The type of object being returned. Namespace types with your IDM product id.
		'type': 'productId:timesheetReviewStatus',

		// Globally unique identifier
		//     If the data in your payload doesn't have a 'real' unique ID from a database that
		//     can be used, the server will need to generate a globally unique id (GUID) for
		//     the response.
		'id' : '077b2871-37c0-4dca-9e16-fe0c7d97ce87',

		// Attributes can contain any number of properties needed to describe the data. However,
		//     once attributes are present, removing them will likely break Pact tests
		// Attributes can contain any primitive value (ie. number, string, bool), dates should
		//     follow this recommendation http://jsonapi.org/recommendations/#date-and-time-fields
		'attributes': {
			'timeStatus':  'Pending',
			'current': 4,
			'persentOfGoal': 105
		}
	}, {
		'type': 'productId:timesheetReviewStatus',
		'id' : '177b2871-4dca-37c0-9e16-fe0c7d97ce88',
		'attributes': {
			'timeStatus':  'Submitted',
			'current': 7,
			'persentOfGoal': 25
		}
	}, {
		'type': 'productId:timesheetReviewStatus',
		'id' : '877b2871-37c0-9e16-4dca-fe0c7d97ce89',
		'attributes': {
			'timeStatus':  'Approved',
			'current': 12,
			'persentOfGoal': 76
		}
	}, {
		'type': 'productId:timesheetReviewStatus',
		'id' : '377b2871-9e16-4dca-37c0-fe0c7d97ce90',
		'attributes': {
			'timeStatus':  'Waiting',
			'current': 8,
			'persentOfGoal': 23
		}
	}, {
		'type': 'productId:timesheetReviewStatus',
		'id' : '877b2871-9e16-37c0-4dca-fe0c7d97ce91',
		'attributes': {
			'timeStatus':  'Rejected',
			'current': 16,
			'persentOfGoal': 89
		}
	}
];


/*@ngInject*/
function vizPieChart() {

	class VizPieChart extends Component {
		/*@ngInject*/
		controller($scope, $http, $log) {
			let highchartInput = Object.assign({}, defaultConfig);

			// TODO: get the data from an actual $http endpoint
			let endpointData = mockEndpointData;

			if(!$scope.widgetTitle){
				$log.warn('$scope.widgetTitle is expected for a Pie Chart.');
			} else {
				highchartInput.title.text = $scope.widgetTitle;
			}

			if(!$scope.nextRoute){
				$log.warn('$scope.nextRoute is an expected field');
			}
			if(!$scope.nextRouteTitle){
				$log.warn('$scope.nextRouteTitle is an expected field');
			}

			var pieDataPoints = endpointData.map(dataPoint => {
				var aPieSection = {};
				if(dataPoint && dataPoint.attributes && dataPoint.attributes[$scope.attrForSectionTitle]){
					aPieSection.name = dataPoint.attributes[$scope.attrForSectionTitle];
				} else {
					$log.warn('A dataPoint did not have the expected attribute called ' + $scope.attrForSectionTitle);
				}

				if(dataPoint && dataPoint.attributes && dataPoint.attributes[$scope.attrForSectionValue]){
					aPieSection.y = dataPoint.attributes[$scope.attrForSectionValue];
				} else {
					$log.warn('A dataPoint did not have the expected attribute called ' + $scope.attrForSectionValue);
				}
				return aPieSection;
			});

			highchartInput.series[0].data = pieDataPoints;

			const vm = $scope.vm = { // eslint-disable-line no-unused-vars
				highchartInput: highchartInput,
				widgetTitle: $scope.widgetTitle,
				nextRoute: $scope.nextRoute,
				nextRouteTitle: $scope.nextRouteTitle
			};

			const go = $scope.go = { // eslint-disable-line no-unused-vars
			};
		}
	}

	return new VizPieChart(props, 'viz-pie-chart', config);
}

// #-- END Component (super-viz-pie-chart) --# //
// #-----------------------------------------# //

angular.module('super-suit-components').directive('superVizPieChart', vizPieChart);
